body {
  justify-content: center;
  display: flex;
  align-items: center;
  background-color: aquamarine;
}


.container{
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 256px; 
  text-align: center;
  border: 10px solid #101116;
  background: #101116;
  border-radius: 10px;
  border-width: 1vh;
  border-color: rgb(122, 138, 243);
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}

input[type = "text"]{
  height: 75px;
  width: 250px;
  background: #10111600;
  border: 0px;
  color: #ffffff;
  text-align: right;
  font-size: 25px;
  font-weight: 900;
  letter-spacing: 1px;
}

#clear{
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;
}

#result{
  grid-column: 3/5;
  grid-row: 5;
  color: #262834;
}

#backspace{
  color: #262834;
}

button{
  margin: 3px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #262834;
  color: white;
  font-weight: 500; 
}

.highlight{
  background-color: rgb(122, 138, 243);
}